<template>
    
</template>
<script>
export default {
    async mounted(){
       try {
        var result = await this.$api.oAuthLogin({
            Code: this.$route.query.code
        });
        this.$user.setUser(result);
        this.$api.setToken(result.t);
        this.$router.replace("/");
      } catch (e) {
        console.error(e);
        if (e.Status == 10) {
          alert("此帳號已被鎖定，請洽客服人員");
        } else {
          alert("登入失敗，帳號或密碼錯誤");
        }
      }
    }
}
</script>